import React from "react";
import { useState, useEffect, useRef} from "react";
import {flushSync} from 'react-dom';
// import 'katex/dist/katex.min.css';
// import 'katex/dist/katex.css';
var Latex = require('react-latex');

function Question({question,totalQuestions,currentQuestion,nextQuestion,setAnswer,selectedAnswers}){
    const [selectedOption, setSelectedOption] = useState(null);
    const timer = useRef(null);
    const progressBar = useRef(null);
    const divRef = useRef(null);
    // timer
    const [timeLeft, setTimeLeft] = useState(120);
    // const [timeUsed, setTimeUsed] = useState(Array.from({length: totalQuestions}, () => 120));
    const intervalRef = useRef(); // Add a ref to store the interval id
    useEffect(() => {
        intervalRef.current = setInterval(() => {
          setTimeLeft((t) => t - 1);
        }, 1000);
        return () => clearInterval(intervalRef.current);
      }, []);
    
      // Add a listener to `timeLeft`
      useEffect(() => {
        if (timeLeft <= 0) {
          clearInterval(intervalRef.current);
        }
      }, [timeLeft]);

    let answerList = ["(A)","(B)","(C)","(D)","(E)"];

    function gotoNextQuestion(index){
        if(timer.current){
            clearTimeout(timer.current);
        }
        flushSync(()=>{
            setAnswer(selectedOption);
            nextQuestion(currentQuestion);
            setSelectedOption(selectedAnswers[currentQuestion])
        });
        
        // console.log('selected answers'+selectedAnswers)
        // setSelectedOption(selectedAnswers[index]);
        // setSelectedOption(null);
    }

    function gotoPreviousQuestion(index){
        if(timer.current){
            clearTimeout(timer.current);
        }
        flushSync(()=>{
            setAnswer(selectedOption);
            nextQuestion(currentQuestion-2);
            setSelectedOption(selectedAnswers[currentQuestion-2])
        });
        // setSelectedOption(null);
    }

    useEffect(()=>{
        progressBar.current.classList.remove("active");
        setTimeout(()=>{
            progressBar.current.classList.add("active");
        },0);
        // scroll question into view on question change
          divRef.current.scrollIntoView({ behavior: "smooth", block: "start" });
          setTimeLeft(120);
        // setSelectedOption(selectedAnswers[currentQuestion])
        // timer.current = setTimeout([],10*1000); // 10 seconds
        // return gotoNextQuestion;
    },[question]);

    // useEffect(()=>{
    //     setSelectedOption(selectedAnswers[currentQuestion])
    // },[currentQuestion,selectedAnswers]);

    return (
        <div className="question" ref={divRef}>
            <div className="progress-bar" ref={progressBar} style={{"animation-delay":timeLeft,}}></div>
            <div className="question-count" style={{"display":"flex", "justifyContent":"space-between","align-items":"center","padding-left":"30px","padding-right":"30px"}}>
                {/* <div style={{"border":"1px solid gray", "padding":"5px", "border-radius":"8px", "display":"flex", "align-items":"center"}}>
                    <span class="material-symbols-outlined">help</span>
                    <b> {currentQuestion} </b>
                    of
                    <b> {totalQuestions} </b>
                </div> */}
                <span>Question {currentQuestion}</span>
                <div style={{"border":"1px solid gray", "padding":"5px", "border-radius":"8px", "display":"flex", "align-items":"center"}}>
                    <span class="material-symbols-outlined">timer</span>
                    <p style={{"display":"inline"}}>{timeLeft}s</p>
                </div>
            </div>
            <div className="main">
                <div className="title">
                    <p className="latexQuestion"><Latex displayMode={question.display}>{question.question}</Latex></p>
                </div>
                <div className="options">
                    {
                        question.choices.map((choice,index)=>{
                            return (
                                <div className={index === selectedOption ? "option active" : "option"}
                                key={index}
                                onClick={()=>setSelectedOption(index)}
                                >
                                    <span>{answerList[index]}</span>
                                    <span><Latex>{choice}</Latex></span>
                                    {/* {answerList[index]} {choice} */}
                                </div>
                            );
                        })
                    }
                </div>
            </div>
            <div className="control">
                <button className={currentQuestion == 1 ? "disabled" : "enabled"} disabled={currentQuestion == 1 ? true : false} onClick={gotoPreviousQuestion}>
                    <span class="material-symbols-outlined">chevron_left</span>
                    Previous
                    </button>
                {/* <div style={{"color":"#8080805e", "padding":"5px", "border-radius":"8px", "display":"flex", "align-items":"center"}}>
                    <span class="material-symbols-outlined">help</span>
                    <b style={{"margin-right":"7px"}}> {currentQuestion} </b>
                    of
                    <b style={{"margin-left":"7px"}}> {totalQuestions} </b>
                </div> */}
                {
                currentQuestion == totalQuestions ? (
                    <button className="submit-exam" onClick={gotoNextQuestion}>
                        Submit
                        <span class="material-symbols-outlined">check</span>
                        </button>
                ) : (
                    <button onClick={gotoNextQuestion}>
                        Next
                        {/* <span class="material-symbols-outlined">arrow_forward_ios</span> */}
                        <span class="material-symbols-outlined">chevron_right</span>
                    </button>
                )
                }
            </div>
        </div>
    );
}

export default Question;